// var _ = require('lodash');
var template = require('lodash/template');

fetch('http://7xqy5q.com1.z0.glb.clouddn.com/index.json?' + (localStorage.timestamp || parseInt(Date.now() / 7200000)))
    .then(function(res) {
        return res.json()
    })
    .then(function (seasons) {
        document.querySelector('.ctn-season').innerHTML = template(document.getElementById('tpl-season').innerHTML)({seasons: seasons});
    });



